.workx_widget{
    background: #F9F9F9;
    border: 1px solid #EEEEEE;
    border-radius: 8px;
    padding: 0 25px 40px 25px;
    position: relative;
    min-height: 220px;
    transition: .3s;
    margin-top: 40px;
    .icon{
        background: $theme_secondary;
        border: 3px solid #FFFFFF;
        box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        width: 85px;
        height: 85px;
        margin: 0 auto;
        margin-top: -44px;
    }
    .context_info{
        max-width: 220px;
        margin: 0 auto;
    }
    h4{
        font-weight: 600;
        font-size: 18px;
        color: $theme_primary;
        margin: 25px auto 12px auto;
        text-transform: capitalize;
        max-width: 180px;
    }
    p{
        font-weight: 400;
        font-size: 15px;
        line-height: 1.5;
        color: #6C6C6C;
    }
    &:hover{
        border-color: $theme_secondary;
        border-width: 1px;
        border-radius: 16px;
    }
}
.col-xl-3:nth-child(2n) .workx_widget .icon  {
    background: $theme_yellow;
}
