.feature_grid{
    display: grid;
    grid-template-columns: repeat( 3, 1fr );
    grid-gap: 38px 24px;
    @media #{$small_mobile} {
        grid-template-columns: repeat(1, 1fr );
    }
    @media #{$tab} {
        grid-template-columns: repeat(2, 1fr );
    }
    &:nth-child(2) .Feature_widget{
        background: #000;
    }
}
.Feature_widget{
    &_content{
        margin-bottom: 18px;
        display: flex;
        align-items: center;
        &_icon{
            width: 60px;
            height: 60px;
            background: rgba(100, 92, 187, 0.15);
            border-radius: 8px;
            flex: 60px 0 0;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        &_title{
            font-weight: 600;
            font-size: 22px;
            line-height: 1.4;
            color: $theme_primary;
            max-width: 140px;
        }
    }
    &_desc{
        color: $gray-color;
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        max-width: 200px;
    }
    &:nth-child(2) .Feature_widget_content .Feature_widget_content_icon {
        background: rgba(249, 148, 23, 0.15);
    }
    &:nth-child(3) .Feature_widget_content .Feature_widget_content_icon {
        background: rgba(22, 199, 255, 0.15);
    }
    &:nth-child(4) .Feature_widget_content .Feature_widget_content_icon {
        background: rgba(11, 190, 61, 0.15);
    }
    &:nth-child(5) .Feature_widget_content .Feature_widget_content_icon {
        background: rgba(255, 0, 0, 0.15);
    }
    &:nth-child(6) .Feature_widget_content .Feature_widget_content_icon {
        background: rgba(66, 0, 255, 0.15);
    }
}